.thanks {
  display: grid;
  place-items: center;
  width: 100%;
  height: 100%;
  .load {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    .pl {
      display: block;
      width: 6.25em;
      height: 6.25em;
    }
    .pl__ring, .pl__ball {
      animation: ring 2s ease-out infinite;
    }
    .pl__ball {
      animation-name: ball;
    }
    
    /* Dark theme  */
    @media (prefers-color-scheme: dark) {
      :root {
        --bg: hsl(var(--hue),10%,10%);
        --fg: hsl(var(--hue),10%,90%);
      }
    }
    
    /* Animation */
    @keyframes ring {
      from {
        stroke-dasharray: 0 257 0 0 1 0 0 258;
      }
      25% {
        stroke-dasharray: 0 0 0 0 257 0 258 0;
      }
      50%, to {
        stroke-dasharray: 0 0 0 0 0 515 0 0;
      }
    }
    @keyframes ball {
      from, 50% {
        animation-timing-function: ease-in;
        stroke-dashoffset: 1;
      }
      64% {
        animation-timing-function: ease-in;
        stroke-dashoffset: -109;
      }
      78% {
        animation-timing-function: ease-in;
        stroke-dashoffset: -145;
      }
      92% {
        animation-timing-function: ease-in;
        stroke-dashoffset: -157;
      }
      57%, 71%, 85%, 99%, to {
        animation-timing-function: ease-out;
        stroke-dashoffset: -163;
      }
    }
  }
  .wrapper {
    display: grid;
    place-items: center;
    gap: 2rem;
    padding-block: 2.5rem;
  }
  .icon {
    aspect-ratio: 1;
    width: 4rem;
  }
  .message {
    p {
      text-align: center;
      color: hsl(231, 11%, 63%);
    }
  }
  @media (min-width: 1024px) {
    .wrapper {
      width: 55%;
    }
  }
}
